import React, { useState, useEffect } from "react";
import axios from "axios";
import confrimImage from "../img/confirm.png";
import cancleImage from "../img/cancel.png";
import flagImage from "../img/flag.png";
import XImage from "../img/close_1.png";
import dragonImage from "../img/dragon-head.png";
import goldCoinImage from "../img/gold-coin.png";
import trolley from "../img/trolley.png";
import { useNuiEvent } from "../hooks/useNuiEvent";

export default function Cashshop() {
  interface userData {
    identifier: string;
    first_name: string;
    last_name: string;
    point: string;
    expire_date_vip: string;
  }

  interface itemsData {
    item_name: string;
    label_name: string;
    limit_type: string;
    max_limit: string;
    point: number;
    original_point: number;
    remaining_quantity: string;
    promotion_flag: string;
    description: string;
  }

  const [data, setData] = useState<userData>({
    identifier: "",
    first_name: "",
    last_name: "",
    point: "",
    expire_date_vip: "",
  });

  const [isConfirmModalOpen, setConfirmModalOpen] = useState(false);
  const [items, setItems] = useState<itemsData[]>([]);
  const [dataToBuy, setDataToBuy] = useState("");
  const [pointToBuy, setPointToBuy] = useState(0);
  const [Message, setMessage] = useState("fail");
  const [isModalBuyOpen, setModalBuyOpen] = useState(false);
  const [inputValue, setInputValue] = useState(1);
  const [hoveredItem, setHoveredItem] = useState("");
  const [authToken, setAuth] = useState("");
  const [isAuthSet, setIsAuthSet] = useState(false);

  useNuiEvent("openCashShop", async (userData) => {
    setAuth(userData.auth);
    setIsAuthSet(true);
    initCashshop();
  });

  useEffect(() => {
    if (isAuthSet) {
      initCashshop();
      initCashshopItems();
    }
  }, [isAuthSet]);

  const initCashshop = async () => {
    const api = "https://mongkol.dev/api/cash-shop/users";
    // const api = "https://localhost:1323/cash-shop/users";
    const headers = {
      // 'Host': 'https://mongkol.dev',
      // 'Origin': 'https://tx.mongkol.dev',
      "Access-Control-Allow-Origin": "https://mongkol.dev",
      // 'Access-Control-Request-Method' : 'GET',
      // 'Access-Control-Request-Headers': 'Authorization',
      // 'Referer': 'https://tx.mongkol.dev',
      Authorization: `Bearer ${authToken}`,
    };

    try {
      const response = await axios.get(api, { headers });
      setData(response.data);
      console.log(response.data);
    } catch (error) {
      console.error("Error sending data:", error);
    }
  };

  const initCashshopItems = async () => {
    const api = "https://mongkol.dev/api/cash-shop/users/items";
    const headers = {
      // 'Host': 'https://mongkol.dev',
      // 'Origin': 'https://tx.mongkol.dev',
      "Access-Control-Allow-Origin": "https://mongkol.dev",
      // 'Access-Control-Request-Method' : 'GET',
      // 'Access-Control-Request-Headers': 'Authorization',
      // 'Referer': 'https://tx.mongkol.dev',
      Authorization: `Bearer ${authToken}`,
    };

    try {
      const response = await axios.get(api, { headers });
      setItems(response.data);
      console.log(response.data);
    } catch (error) {
      console.error("Error sending data:", error);
    }
  };

  const buyItem = async (itemName: string, quantity: number, point: number) => {
    console.log(authToken);
    const api = "https://mongkol.dev/api/cash-shop/users/buy";
    const headers = {
      // 'Host': 'https://mongkol.dev',
      // 'Origin': 'https://tx.mongkol.dev',
      "Access-Control-Allow-Origin": "https://mongkol.dev",
      // 'Access-Control-Request-Method' : 'GET',
      // 'Access-Control-Request-Headers': 'Authorization',
      // 'Referer': 'https://tx.mongkol.dev',
      Authorization: `Bearer ${authToken}`,
    };
    console.log(itemName, quantity);
    const requestData = {
      name: itemName,
      quantity: quantity,
      point: point,
    };
    console.log(requestData);

    try {
      const response = await axios.put(api, requestData, { headers });
      console.log(response.data);
      await initCashshop();
      await initCashshopItems();
      setInputValue(1);
      setModalBuyOpen(true);
      setMessage(response.data.Message);
    } catch (error) {
      console.error("Error sending data:", error);
      setMessage("fail");
    }
  };

  const openConfirmModal = (itemName: string, point: number) => {
    setDataToBuy(itemName);
    setPointToBuy(point);
    setConfirmModalOpen(true);
  };

  const closeConfirmModal = () => {
    setDataToBuy("");
    setConfirmModalOpen(false);
  };

  const handleConfirm = () => {
    if (dataToBuy && pointToBuy) {
      buyItem(dataToBuy, inputValue, pointToBuy);
    }
    closeConfirmModal();
  };

  const handleCancel = () => {
    closeConfirmModal();
  };

  const handleฺBuyModal = () => {
    setModalBuyOpen(false);
  };

  return (
    <div>
      <div className="rounded-lg p-[1vh] relative">
        <div className="pt-[3.5vh]">
          <div className="flex items-center ">
            {/* <div className="logo-container w-[35%] font-mok font-bold flex flex-col items-center justify-center text-[4.5vh]">
              <div className="text-[2vh]">THE</div>
              <div className="leading-[5.5vh]">MYSTERY</div>
              <div className="leading-[6vh]">MAN</div>
            </div>
            <div className="border-img w-[65%] flex items-center">
              <div className="flex flex-col items-center justify-end font-[500] w-full mt-[2.5vh]">
                <div className="text-[3.8vh] leading-[1vh] mb-[2vh] flex items-center justify-end w-full mr-[3.5vh] ">
                  <span>
                    {data.first_name} {data.last_name}
                  </span>
                </div>
                <div className="flex items-center justify-end w-full ">
                  <div className="point-container flex items-center justify-end text-[3.8vh] h-[6.2vh] w-[38vh] ">
                    <div className="ml-[1.5vh]">
                      {parseInt(data.point).toLocaleString()}
                    </div>
                    <div className="flex justify-center items-center">
                      <div className="ml-[2vh] pr-[2vh]  ">CC</div>
                    </div>
                  </div>
                </div>
              </div>
            </div> */}
            <div
              className="bg-black/[0.5] text-white font-bold text-[2vh] w-full rounded-[0.5vh] flex flex-row-reverse items-center relative"
              style={{ boxShadow: "0px 0px 4px rgba(0, 0, 0, 0.8)" }}
            >
              <img
                src={dragonImage}
                className="w-[16vh] h-[11vh] absolute mb-[12.8vh]"
              />
              <div
                className="bg-[#ffa800]/[0.5] w-[20vh] h-[3vh] m-[0.5vh] rounded-[0.5vh]  ml-[1vh] flex flex-row-reverse items-center"
                style={{ boxShadow: "0px 0px 3px rgba(0, 0, 0, 0.3)" }}
              >
                <img
                  src={goldCoinImage}
                  className="w-[2.5vh] h-[2.5vh] ml-[1vh]"
                />
                <div>{parseInt(data.point).toLocaleString()}</div>
              </div>
              <div>
                {" "}
                {data.first_name} {data.last_name}
              </div>
            </div>
          </div>
          <div className="mt-[2vh] text-white">
            <div
              className="gradient-gold  p-[2vh] h-[50vh] font-[500] text-[1.4vh] rounded-[0.5vh] overflow-y-scroll "
              style={{ boxShadow: "0px 0px 4px rgba(0, 0, 0, 0.8)" }}
            >
              <div className="grid grid-cols-5 gap-[2vh]" id="cash-items">
                {items.map((item: itemsData) => (
                  <div
                    className="border-[0.2vh] border-gray rounded-[0.5vh] gradient w-fit px-[0.8vh] pt-[0.5vh] pb-[0.3vh] relative"
                    style={{ boxShadow: "0px 0px 2px rgba(0, 0, 0, 0.8)" }}
                  >
                    <div className="text-center">{item.label_name}</div>
                    <div className="flex justify-center mx-[1vh]">
                      <div
                        className="box-content w-[12vh] h-[12vh] relative"
                        onMouseEnter={() => setHoveredItem(item.item_name)}
                        onMouseLeave={() => setHoveredItem("")}
                      >
                        <img
                          src={`nui://nc_inventory/html/img/items/${item.item_name}.png`}
                          className="w-[12vh] h-[12vh]"
                          alt="รูปภาพ"
                        />
                        {item.limit_type != "00" &&
                          item.remaining_quantity == "0" && (
                            <img
                              src={XImage}
                              className="w-[12vh] h-[12vh] absolute inset-0 opacity-60 "
                              alt="รูปภาพ"
                            />
                          )}
                      </div>
                    </div>
                    <div className="text-[1.3vh] relative ">
                      <div className="flex items-center justify-center h-[1.3vh] mb-[0.2vh]">
                        {item.limit_type === "00"
                          ? ""
                          : `${item.remaining_quantity}/${item.max_limit}`}
                      </div>
                      <div className=" h-[0.3vh] bg-gray-300/[0.5]  rounded-full"></div>
                    </div>
                    <div className="relative my-[0.21vh] flex items-center justify-center h-[2vh]">
                      <div className="absolute left-0 text-[1.3vh] items-center">
                        <span className="relative h-[1.3vh] flex items-center">
                          {item.promotion_flag === "on" && (
                            <>
                              <span className="text-white/[0.7]">
                                {item.original_point}
                              </span>
                              <div className="absolute h-[0.2vh] w-full bg-white/[0.7] rounded-full z-10"></div>
                            </>
                          )}
                        </span>
                      </div>

                      {item.promotion_flag === "on" && (
                        <div className="absolute right-0 bg-red-500 px-[0.5vh] rounded-[0.5vh] text-[1.2vh]">
                          {(
                            ((item.original_point - item.point) /
                              item.original_point) *
                            100
                          ).toFixed(0)}
                          %
                        </div>
                      )}

                      <div className="flex items-center justify-center text-[1.8vh]">
                        {item.point} G
                      </div>
                    </div>

                    <button
                      className="bg-color h-[2.8vh] w-full flex items-center justify-center font-[500] px-[1vh] rounded-[0.5vh]"
                      onClick={() =>
                        openConfirmModal(item.item_name, item.point)
                      }
                    >
                      <img src={trolley} className="w-[2.1vh] h-[2.1vh]" />
                    </button>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
        {isConfirmModalOpen && (
          <div className="model-container rounded-[0.8vh] w-fit p-[3vh] text-white  absolute">
            <div className="flex justify-center flex-col">
              <div className="text-[1.8h] text-center font-bold">
                ยืนยันยอมรับการแลกเปลี่ยน ?
              </div>
              <input
                type="number"
                className="bg-black/[0.7] rounded-[0.5vh]  text-center items-center justify-center my-[1vh] h-[2.8vh]"
                placeholder="จำนวน"
                value={inputValue}
                onChange={(e) => {
                  const inputValue = parseInt(e.target.value, 10);
                  if (!isNaN(inputValue)) {
                    setInputValue(inputValue);
                  }
                }}
              />
              <div className="grid grid-cols-2 gap-[1vh]">
                <button
                  className="btn-confirm h-[2.3vh] w-full rounded-md text-white select-none whitespace-nowrap flex items-center justify-center"
                  onClick={handleConfirm}
                >
                  <div className="box-content h-[1.7] w-[1.7vh]">
                    <img
                      src={confrimImage}
                      className="box-content h-[1.5] w-[1.5vh] mb-[0.2vh]"
                    />
                  </div>
                </button>
                <button
                  className="btn-cancle h-[2.3vh] w-full rounded-md text-white select-none whitespace-nowrap flex items-center justify-center"
                  onClick={handleCancel}
                >
                  <div className="box-content h-[1.7] w-[1.7vh]">
                    <img
                      src={cancleImage}
                      className="box-content h-[1.5] w-[1.5vh] mb-[0.2vh]"
                    />
                  </div>
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
      {isModalBuyOpen && (
        <div
          className="model-container rounded-[0.8vh] w-fit p-[3vh] text-white font-bold absolute"
          style={{ boxShadow: "0px 0px 4px rgba(0, 0, 0, 0.8)" }}
        >
          <div className="text-center">
            {Message == "fail" ? "ทำการซื้อล้มเหลว" : "ทำการซื้อสำเร็จ"}
          </div>
          <div className="flex justify-center mt-[1vh]">
            <button
              className={
                Message == "fail"
                  ? " btn-cancle h-[2.3vh] px-[4vh] rounded-[1vh] text-white select-none whitespace-nowrap flex items-center justify-center"
                  : " btn-confirm  h-[2.3vh] px-[4vh] rounded-[1vh] text-white select-none whitespace-nowrap flex items-center justify-center"
              }
              onClick={handleฺBuyModal}
            >
              <div className="box-content h-[1.7] w-[1.7vh]">
                <img
                  src={confrimImage}
                  className="box-content h-[1.5] w-[1.5vh] mb-[0.2vh]"
                  alt="Confirm Image"
                />
              </div>
            </button>
          </div>
        </div>
      )}
      {items.map((item: itemsData) => (
        <div key={item.item_name}>
          <div>
            {hoveredItem === item.item_name && (
              <div className="absolute right-[2%] top-0 bottom-0 flex justify-center items-center">
                <div className="max-w-sm gradient-gold rounded-[1vh] shadow-2xl p-4 relative">
                  <div className="px-6 py-4">
                    <div className="font-bold text-[2vh] mb-[1vh] text-yellow-300">
                      {item.label_name}
                    </div>
                    <p className="text-white text-[1.5vh] text-shadow ">
                      {item.description}
                    </p>
                  </div>
                  {/* Shield icon at the bottom right corner */}
                  <div className="absolute bottom-[1vh] right-[0.5vh]">
                    <img
                      src={`nui://nc_inventory/html/img/items/${item.item_name}.png`}
                      className="w-[3vh] h-[3vh]"
                      alt="รูปภาพ"
                    />
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      ))}
    </div>
  );
}
